#leftbar-cont{
  background-color: white;
  border-right: 1px solid rgb(236, 229, 229);
  height: 100vh;
  justify-content: center;
  row-gap: 1px;
  padding-top: 10vh;
  position: sticky;
  top: 0;
}
#feed{
  margin: auto;
  display: grid;
  justify-content: center;
  align-items: center;
  padding-top: 10vh;
  background-color: #f8fbfd;
  padding-bottom: 20vh;
}
#nav{
  margin-bottom: 10vh;
}
#add{
  height: 1vh;
  width: 1vw;
}
#friends{
  margin: auto;
  align-items: center;
  justify-content: center;
}
#rightbar-cont{
  padding-top: 10vh;
  display: grid;
  justify-content: center;
  align-items: center;
  position: sticky;
  top: 0;
  grid-template-columns: auto;
}
#gallery{
  margin-top: 5vh;
  text-align: center;
}




